<template>
  <div class="detail">
    <my-nav></my-nav>
    <div class="container">
      <img :src="detailData.albums" alt="" class="headImage">
      <h1>{{detailData.title}}</h1>
      <table>
        <tr>
          <td class="tit">关键字</td>
          <td>{{detailData.tags}}</td>
        </tr>
        <tr>
          <td class="tit">介绍</td>
          <td>{{detailData.imtro}}</td>
        </tr>
        <tr>
          <td class="tit">材料</td>
          <td>{{detailData.ingredients}}</td>
        </tr>
        <tr>
          <td class="tit">原料</td>
          <td>{{detailData.burden}}</td>
        </tr>
        <tr>
          <td class="tit">步骤</td>
          <td>
            <ul>
              <li v-for="item of detailData.steps">
                <img :src="item.img" alt="">
                <p>{{item.step}}</p>
              </li>
            </ul>
          </td>
        </tr>
      </table>

    </div>

  </div>
</template>

<script>
  export default {
    name: 'Detail',
    data () {
      return {
        detailData: {
          id:"1",
          title:"",
          tags:"",
          imtro:"",
          ingredients:"",
          burden:"",
          albums:[],
          steps:[
            {
              img:"",
              step:""
            },
          ]
        }
      }
    },
    mounted: function () {
      let id = this.$route.params.id;
      let params = {
        key: '5c1abcdb494382186bf2ea20a6aff5a4',
        id
      }
//      this.$http.post('/api/queryid', params,
//        {
//          emulateJSON:true
//        }).then(function(response){
//        console.log(JSON.parse(response.bodyText).result.data)
//        this.detailData = JSON.parse(response.bodyText).result.data[0];
//
//      },function(err){
//        console.log(err)
//      });
      $.getJSON('/api/queryid',params,(data)=>{
        this.detailData = data.result.data[0];
      },"json")
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .detail{
    background-color: #eeeeee;
  }
  .container{
    padding: 10px;

  }
  .tit{
    width:50px;
    text-align: center;
    font-weight: bold;
    vertical-align: top;
  }
  .headImage{
    width:100%;
  }
  ul{
    padding-top: 10px;
  }
  li p{
    padding-top:5px;
    padding-bottom: 10px;
  }
</style>



