<template>
  <div class="login">
    <input type="text" v-model="msg" v-bind:style="{'border':bd}" lazy>
    <h1 v-my-directive="msg"></h1>
  </div>
</template>

<script>
  import Vue from 'vue'
  Vue.directive('my-directive',function (el,binding){
    if(binding.value.length>0 && binding.value.length<4 || binding.value.length > 8){
      //this.vm.bd = '1px solid red';
      el.innerHTML = '请输入4-8位';
      el.style.display = 'block';
    }else if(/^\d+$/.test(binding.value)){
      el.innerHTML = '不能为纯数字';
      el.style.display = 'block';
    }else{
      el.style.display = 'none';
    }
  })

  export default {
    name: 'Login',
    data () {
      return {
        msg : '',
        bd : ''
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .nav{
    height: 40px;
    background-color: #ffffff;
  }
  .btn{
    height:40px;
    width:40px;
    line-height: 40px;
    text-align: center;
  }
</style>



