<template>
  <div id="app">
    <router-view />
    <my-loading v-bind:show="show"></my-loading>
    <transition name="fade">
      <div v-if="getterLoginPageState" class="loginLayer">
        <div class="form">
          <input type="text" class="account" v-model="loginInfo.account">
          <input type="password" class="password" v-model="loginInfo.password">
          <button @click="changeLoginPageStateFalse(loginInfo)" class="loginBtn">登陆</button>
          <div class="tip">
            <a href="" class="tipLeft">忘记密码</a>
            <a href="" class="tipRight">新用户注册</a>
          </div>
        </div>
        <div class="footerTip">
          登陆即代表阅读并同意<a href="" class="blue">服务条款</a>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';
export default {
  name: 'app',
  props:['show'],
  data () {
    return {
      loginInfo:{
        account:'',
        password:''
      }
    }
  },
  computed: {...mapGetters(['getterLoginPageState'])},
  methods: {
    ...mapActions(['changeLoginPageStateFalse']),
  }
}
</script>

<style>
#app {
  height: 100%;
  width: 100%;
  background-color: #eeeeee;
  position: relative;
}
  .loginLayer{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.8);
    top:0;
    left: 0;
  }
.fade-enter-active, .fade-leave-active {
  transition: all .5s;
}
.fade-enter, .fade-leave-to{
  left: -999px;
}
  .form{
    padding: 30px;
    padding-top: 120px;
    position: relative;
  }
  .account,.password{
    width: 100%;
    height: 40px;
    background-color: transparent;
    border:none;
    border-bottom:1px solid #fff;
    outline: none;
    color: #fff;
    font-size: 18px;
  }
  .loginBtn{
    width: 100%;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: #29b4f0;
    font-weight: bold;
    color: #ffffff;
    font-size: 16px;
    margin-top: 40px;
    margin-bottom: 20px;
  }
  .tip{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .tipLeft,.tipRight{
    color: #29b4f0;
    font-size: 14px;
    font-family: "Microsoft YaHei";
  }
  .footerTip{
    position: absolute;
    bottom: 30px;
    color: #fff;
    width: 100%;
    text-align: center;
  }
  .blue{
    color: #29b4f0;
    font-size: 12px;
  }
</style>



