export const changeMsg = ({dispatch,commit,state}) => {
    if(state.loginState){
      dispatch('loginOut')
    }else{
      dispatch('changeLoginPageStateTrue')
    }
};

//登陆
export const loginIn = ({commit},loginInfo) => {

    commit({
      type: 'mutationsMsg',     //对应mutation.js中的mutationsMsg方法
      userInfo:{
        account:loginInfo.account
      }
    });
    commit({
      type: 'mutationsLoginState',     //对应mutation.js中的mutationsLoginStateg方法
      loginState:true
    });


};

//注销
let initUserInfo = {
  account:'',
}
export const loginOut = ({commit,state},userInfo) => {

    commit({
      type: 'mutationsMsg',     //对应mutation.js中的mutationsMsg方法
      userInfo:initUserInfo
    });
    commit({
      type: 'mutationsLoginState',     //对应mutation.js中的mutationsLoginStateg方法
      loginState:false
    });
};

//显示loginPage
export const changeLoginPageStateTrue = ({dispatch,commit,state}) => {
  commit({
    type: 'mutationsLoginPageState',     //对应mutation.js中的mutationsMsg方法
    isShowLoginPage:true
  });
};
//隐藏loginPage
export const changeLoginPageStateFalse = ({dispatch,commit,state},loginInfo) => {
  //模拟异步请求
  setTimeout(()=>{
    commit({
      type: 'mutationsLoginPageState',     //对应mutation.js中的mutationsMsg方法
      isShowLoginPage:false
    });
    dispatch('loginIn',loginInfo)
  },3000)

};



