export const mutationsMsg = (state, payload) => {
  state.userInfo = payload.userInfo;
}
export const mutationsLoginState = (state,loginState) => {
  state.loginState = loginState.loginState;
}
export const mutationsLoginPageState = (state,isShowLoginPage) => {
  state.isShowLoginPage = isShowLoginPage.isShowLoginPage;
}



