<template>
  <div class="list">
    <my-nav></my-nav>
    <ul>
      <li v-for="item of list" v-bind:style="{backgroundImage:'url('+item.albums+')'}">
        <router-link :to="{name: 'Detail',params: {id :item.id}}">
          <h1><em>{{item.title}}</em></h1>
          <div>
            <i class="fa fa-chevron-right go"></i>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  import httpRequest from '../network'
  import ajax from '../network/ajax'
  import $ from 'jquery'
  export default {
    name: 'List',
    data () {
      return {
        list: []
      }
    },
    mounted: function () {
      let {dishName,dishId} = this.$route.params;
//      ajax({
//        method: 'GET',
//        url: '/api/nba',
//        data: {
//          key: '54b40fdd27e818b2bff7c58538a5b687'
//        },
//        success: function (response) {
//          console.log(response)
//        }
//      })

//      httpRequest('/api/nba', 'POST', {}, (data) => {
//        console.log(data)
//      })
      let url,params = {
        key: '5c1abcdb494382186bf2ea20a6aff5a4'
      }

      if(dishName){
        params.menu = dishName;
        url = '/api/query.php';
      }else{
        params.cid = dishId;
        url = '/api/index';
      }
//      this.$http.post(url, params,
//        {
//          emulateJSON:true
//        }).then(function(response){
//          console.log(response)
//        this.list = JSON.parse(response.bodyText).result.data;
//
//      },function(err){
//        console.log(err)
//      });


      $.getJSON(url,params,(data)=>{
        this.list = data.result.data;
      },"json")
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .list{
    background-color: #eeeeee;
  }
  li{
    height: 100px;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  li>*{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.3);
  }
  li h1{
    font-size: 20px;
    font-weight: bold;
    color: #fff;
    padding: 10px;
  }
  li div{
    display: flex;
    align-items: center;
  }
  .go{
    font-size: 20px;
    color: white;
  }
</style>



