<template>
  <div class="homeMore">
    <div class="container">
      <p>{{'账户：'+gettersMsg.account}}</p>
      <button @click="changeMsg">{{getterLoginState?'注销':'登陆'}}</button>
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';
  export default {
    name: 'HomeMore',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    },
    computed: {...mapGetters(['gettersMsg','getterLoginState'])},
    methods: {
      ...mapActions(['changeMsg']),

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .homeMore{

  }
</style>



